import React from "react";
import "./contact.css";

export default function Contact() {
  return (
    <>
      <div className='contact-us'>
        <div className='text'>
          <p className='touch'>GET IN TOUCH</p>
          <h1>CONTACT US </h1>
          <div className='hr-div'>
            <hr className='hr' />
            <hr className='hr2' />
          </div>

          <p className='desciption'>
            Praesent dignissim nibh quis ante dignissim, in posuere dui feugiat.
            duis elit dui, vestibulum nec ipsum id, finibus sollicitudin elit.
            cras et purus sagittis.
          </p>
        </div>
        <div className='form'>
          <div className='text-field'>
            <input type='text' placeholder='Name' className='input' />
            <input type='email' placeholder='Email Name' className='input' />
          </div>
          <input type='text' placeholder='Subject' className='subject' />
          <textarea placeholder='Message' className='msg' />
          <span className='term'>
            <input type='checkbox' className='check-box' />
            <p>
              I have read and agree to Afnan <span>terms and conditions</span>.
            </p>
          </span>
          <div className='btn-div'>
            <button id='btn' type='submit'>
              Send
            </button>
          </div>
        </div>
      </div>
    </>
  );
}
